body {
  padding-bottom: 2rem;
  box-shadow: inset 3rem 3rem 10rem rgba(0, 0, 0, 0.2),
    inset -3rem -3rem 10rem rgba(0, 0, 0, 0.2);
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.image {
  width: 370px;
  margin-top: 50px;
}
@media (max-width: 770px) {
  .container {
    margin: 0 10%;
  }
  .image {
    width: 100%;
  }
}
